import React from 'react';
import ReactDOM from 'react-dom';

import { trans } from '@mssgme/shared';
import RedirectApp from './App';

const data = window.INITIAL_DATA;
const { lang } = window.document.documentElement;

trans.extend(window.LOCALES);

ReactDOM.render(<RedirectApp data={data} lang={lang} />, document.getElementById('root'));
